// base sass, mixin + variable
@import 'base/vars';
@import 'base/mixin/mixin';
@import 'base/mixin/grid';
@import 'base/mixin/grid-framework';

.icon {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: 55%;
	display: block;
	width: 100%;
	height: 100%;
	@include min-responsive($screen-sm-min) {
		background-size: 50%;
	}
	&-close {
		background-image: url(data:image/svg+xml;utf8;base64,);
	}
	&-arrow-right {
		background-image: url(data:image/svg+xml;utf8;base64,);		
	}
	&-arrow-left {
		background-image: url(data:image/svg+xml;utf8;base64,);	
	}
	&-user {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-cart {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-cart-black {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-search {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-facebook {
		background-image: url(data:image/svg+xml;base64,);		
	}
	&-instagram {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-twitter {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-right{
		background-image: url(data:image/svg+xml;base64,);
	}
	&-star-on {
        background-image: url(data:image/svg+xml;base64,);
    }
    &-star-off {
        background-image: url(data:image/svg+xml;base64,);
	}
	&-chevron-left {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-chevron-right {
		background-image: url(data:image/svg+xml;base64,);
	}
	&-bar {
		display: inline-block;
		width: 100%;
		height: 100%;
		//background: #000;
		cursor: pointer;
		text-decoration: none;
		color: #000;
		text-align: center;
		padding: 8px;
		@include min-responsive($screen-sm-min) {
			padding: 13px 8px;
		}
		& i {
			position: relative;
			display: inline-block;
			width: 100%;
			height: 2px;
			color: #000;
			font: bold 14px/.4 Helvetica;
			text-transform: uppercase;
			text-indent: -105px;
			background: #000;
			transition: all .2s ease-out;
			&::before, 
			&::after {
				content: '';
				width: 80%;
				height: 2px;
				background: #000;
				position: absolute;
				right: 0;
				transition: all .2s ease-out;
			}
			&::before {
				top: -9px;
			}
			&::after {
				bottom: -9px;
			}
		}
		&.active {
			& i {
				background: transparent;
				&::before {
					width: 100%;
					top: 0;
					background: #000;
					-webkit-transform: rotateZ(-45deg);
					-moz-transform: rotateZ(-45deg);
					-ms-transform: rotateZ(-45deg);
					-o-transform: rotateZ(-45deg);
					transform: rotateZ(-45deg);
				}
				&::after {
					width: 100%;
					bottom: 0;
					background: #000;
					-webkit-transform: rotateZ(45deg);
					-moz-transform: rotateZ(45deg);
					-ms-transform: rotateZ(45deg);
					-o-transform: rotateZ(45deg);
					transform: rotateZ(45deg);
				}
			}
		}		
	}
}
